import { Formik } from 'formik';
import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { validationSchema } from 'helpers/validationSchema';
import { levels } from 'constants/languageLevels';
import {
  Form,
  InputPositioning,
  Label,
  TextTypeInput,
  HelperText,
  RadioGroupText,
  RadioGroup,
  RadioGroupLabel,
  RadioGroupInput,
  FileInput,
  FileInputLabel,
  FileInputUpload,
  FileInputPlaceholder,
  PhoneMask,
} from './modalForm.styled';

const ModalForm = props => {
  const [nameFilled, setNameFilled] = useState(false);
  const [emailFilled, setEmailFilled] = useState(false);
  const [phoneFilled, setPhoneFilled] = useState(false);
  const [radioBtnChecked, setRadioBtnChecked] = useState(false);
  const [fileName, setFileName] = useState('');

  console.log(radioBtnChecked);

  const handleChange = e => {
    if (e.target.name === 'name') setNameFilled(true);
    if (e.target.name === 'email') setEmailFilled(true);
    if (e.target.name === 'phone') setPhoneFilled(true);
    if (e.target.name === 'position_id') setRadioBtnChecked(true);
  };

  const handleSubmit = (values, { resetForm }) => {
    const userData = new FormData();
    userData.append('name', values.name);
    userData.append('email', values.email);
    userData.append('phone', values.phone);
    userData.append('photo', values.photo);
    userData.append('position_id', values.position_id);

    // if (tokenFetched) signUp(userData);

    resetForm();
    document.getElementById(values.position_id).checked = false; //formik doesn't reset radio buttons properly
    setNameFilled(false);
    setEmailFilled(false);
    setPhoneFilled(false);
    setFileName('');
    // setPage(1);
  };

  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title
          id="contained-modal-title-vcenter"
          className="me-auto ms-auto"
        >
          Apply for job of your dream!
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Formik
          initialValues={{
            name: '',
            email: '',
            phone: '',
            position_id: null,
            photo: null,
          }}
          validationSchema={validationSchema}
          onSubmit={(values, actions) => handleSubmit(values, actions)}
        >
          {formik => (
            <Form onSubmit={formik.handleSubmit} onChange={handleChange}>
              <div>
                <InputPositioning>
                  {nameFilled && <Label>Your name</Label>}
                  <TextTypeInput
                    id="name"
                    name="name"
                    type="text"
                    border={formik.errors.name && '1px solid red'}
                    placeholder={nameFilled ? '' : 'Your name'}
                    onChange={formik.handleChange}
                    {...formik.getFieldProps('name')}
                  />
                  {formik.touched.name && formik.errors.name ? (
                    <HelperText>{formik.errors.name}</HelperText>
                  ) : null}
                </InputPositioning>

                <InputPositioning>
                  {emailFilled && <Label>Email</Label>}
                  <TextTypeInput
                    id="email"
                    type="email"
                    name="email"
                    border={formik.errors.name && '1px solid red'}
                    placeholder={emailFilled ? '' : 'Email'}
                    onChange={formik.handleChange}
                    {...formik.getFieldProps('email')}
                  />
                  {formik.touched.email && formik.errors.email ? (
                    <HelperText>{formik.errors.email}</HelperText>
                  ) : null}
                </InputPositioning>

                <InputPositioning>
                  {phoneFilled && <Label>Phone</Label>}
                  <TextTypeInput
                    id="phone"
                    type="text"
                    name="phone"
                    border={formik.errors.name && '1px solid red'}
                    placeholder={phoneFilled ? '' : 'Phone'}
                    onChange={formik.handleChange}
                    {...formik.getFieldProps('phone')}
                  />
                  <PhoneMask>+380XXXXXXXXX</PhoneMask>

                  {formik.touched.phone && formik.errors.phone ? (
                    <HelperText>{formik.errors.phone}</HelperText>
                  ) : null}
                </InputPositioning>
              </div>

              <RadioGroupText>Point your English level</RadioGroupText>

              <RadioGroup>
                {levels.map(level => (
                  <RadioGroupLabel key={level.id}>
                    <RadioGroupInput
                      id={level.id}
                      type="radio"
                      name="position_id"
                      value={level.id}
                      onChange={formik.handleChange}
                    />
                    {`${level.id} ${level.name}`}
                  </RadioGroupLabel>
                ))}
              </RadioGroup>

              <FileInput
                id="photo"
                name="photo"
                type="file"
                placeholder="Upload your photo"
                onChange={e => {
                  formik.setFieldValue('photo', e.currentTarget.files[0]);
                  setFileName(e.currentTarget.files[0].name);
                }}
              />
              <FileInputLabel htmlFor="photo">
                <FileInputUpload
                  border={formik.errors.photo && '1px solid red'}

                  // className={
                  //   formik.touched.photo && formik.errors.photo
                  //     ? 'FormFileInputUpload formInputError'
                  //     : 'formFileInputUpload'
                  // }
                >
                  Upload
                </FileInputUpload>
                <FileInputPlaceholder
                  border={formik.errors.photo && '1px solid red'}

                  // className={
                  //   formik.touched.photo && formik.errors.photo
                  //     ? 'formFileInputPlaceholder uploadPlaceholderError'
                  //     : 'formFileInputPlaceholder' && fileName
                  //     ? ' formFileInputPlaceholder cutPlaceholder'
                  //     : 'formFileInputPlaceholder'
                  // }
                >
                  {fileName ? fileName : 'Upload your CV'}
                </FileInputPlaceholder>
              </FileInputLabel>
              {formik.touched.photo && formik.errors.photo ? (
                <HelperText>{formik.errors.photo}</HelperText>
              ) : null}

              {/* <button
                type="submit" 
               disabled={buttonIsActive ? false : true}
               className={
                 buttonIsActive ? 'signUpButton' : 'signUpButton disabled'
                }
              >
           Sign up
            </button>  */}
            </Form>
          )}
        </Formik>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="outline-success" className="me-auto ms-auto" size="lg">
          Apply
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ModalForm;
